
import { useState } from 'react';
import './App.css';

function App() {
  const [calc, setCalc] = useState("");

  const ops = ["/", "*", "+", "-", "."];

  const add = (value) => {
    const operatorPattern = /[+\-*/]/;
    if (calc === "" && value === "0") {
      return;
    }
    if (value === ".") {
      const parts = calc.split(operatorPattern);
      if (parts[parts.length - 1].includes(".")) {
        return;
      }
    }
    if (value !== "-" && operatorPattern.test(value)) {
      const lastChar = calc[calc.length - 1] || "";
      const secondLastChar = calc[calc.length - 2] || "";
      if (operatorPattern.test(lastChar)) {
        if (lastChar === "-" && operatorPattern.test(secondLastChar)) {
          setCalc(calc.slice(0, -2) + value);
          return;
        }
        setCalc(calc.slice(0, -1) + value);
        return;
      }
    }

    setCalc(calc + value);
  };
  const calculate = () => {
    setCalc(eval(calc).toString());
  };
  const clearAll = () => {
    setCalc("");
  };

  return (
    <>
    <div className='con'>
          <div id='display' className="d">
          {calc || "0"}
          </div>
      <div className='btns'>
      <div className="row">
        <button onClick={clearAll} className = "col-6 col-sm-6 col-md-6" id='clear'>AC</button>
        <button onClick={()=>{add('/')}} className = "col-3 col-sm-3 col-md-3" id='divide'>/</button>
        <button onClick={()=>{add('*')}} className = "col-3 col-sm-3 col-md-3" id='multiply'>X</button>
        </div>
        <div className="row">
        <button onClick={()=>{add('7')}} className = "col-3 col-sm-3 col-md-3" id='seven'>7</button>
        <button onClick={()=>{add('8')}} className = "col-3 col-sm-3 col-md-3" id='eight'>8</button>
        <button onClick={()=>{add('9')}} className = "col-3 col-sm-3 col-md-3" id='nine'>9</button>
        <button onClick={()=>{add('-')}} className = "col-3 col-sm-3 col-md-3" id='subtract'>-</button>
        </div>
        <div className="row">
        <button onClick={()=>{add('4')}} className = "col-3 col-sm-3 col-md-3" id='four'>4</button>
        <button onClick={()=>{add('5')}} className = "col-3 col-sm-3 col-md-3" id='five'>5</button>
        <button onClick={()=>{add('6')}} className = "col-3 col-sm-3 col-md-3" id='six'>6</button>
        <button onClick={()=>{add('+')}} className = "col-3 col-sm-3 col-md-3" id='add'>+</button>
        </div>
        <div className='row'>
        <button onClick={()=>{add('1')}} className = "col-3 col-sm-3 col-md-3" id='one'>1</button>
        <button onClick={()=>{add('2')}} className = "col-3 col-sm-3 col-md-3" id='two'>2</button>
        <button onClick={()=>{add('3')}} className = "col-3 col-sm-3 col-md-3" id='three'>3</button>
        <button onClick={calculate} className = "col-3 col-sm-3 col-md-3 " id='equals'>=</button>
        </div>
        <div className="row">
        <button onClick={()=>{add('0')}} className = "col-6 col-sm-6 col-md-6" id='zero'>0</button>
        <button onClick={()=>{add('.')}} className = "col-3 col-sm-3 col-md-3" id='decimal'>.</button>
        <button className = "col-3 col-sm-3 col-md-3" id='lg'>Made by Sun</button>
        </div>
        </div>
      </div>
    </>
  );
}

export default App;
